export default {
  name: 'PwdInputWrap',
  props: {
    field: {
      type: String,
      default: 'password',
    },
  },
  data() {
    return {
      eyeOpen: false,
      userInput: '',
      isValidated: true,
      pwdConditions: [
        {
          text: '長度為 8 - 15 位字元',
          conditionName: 'length',
          isValidated: false,
        },
        {
          text: '包含英文字母大寫與小寫',
          conditionName: 'case',
          isValidated: false,
        },
        {
          text: '包含一個數字',
          conditionName: 'hasNumber',
          isValidated: false,
        },
      ],
    }
  },
  methods: {
    handleEyeOpen() {
      this.eyeOpen = !this.eyeOpen
    },
    handleInput(event) {
      this.$emit('input', event.target.value)
      this.handleMulValidate(this.field)
    },
    async handleMulValidate() {
      const invalidations = await this.$validateFunctions.mulPwdVal(
        this.userInput
      )
      this.pwdConditions.forEach((element) => {
        if (invalidations.includes(element.conditionName)) {
          element.isValidated = false
        } else element.isValidated = true
      })
    },
  },
}
