export default {
  name: 'Register',
  layout: 'account',
  data() {
    return {
      modalOpen: false,
      register: {
        account: '',
        settingPassword: '',
        confirmPassword: '',
      },
    }
  },
  methods: {
    async userRegister() {
      if (
        !(await this.$validateFunctions.allFormValidate(
          'register',
          this.register
        ))
      ) {
        alert('格式不對，請檢查後重填')
        return false
      }
      if (this.register.settingPassword !== this.register.confirmPassword) {
        alert('確認密碼與設定密碼不一致，請檢查後重填')
        return false
      }
      // try {
      //   await this.$axios.post('register', {
      //     username: this.username,
      //     email: this.email,
      //     password: this.password,
      //   })
      this.handleModalOpen()
      this.$validateFunctions.userLogin({
        account: this.register.account,
        password: this.register.confirmPassword,
      })
      //   this.$router.push('/')
      // } catch (e) {
      //   this.error = e.response.data.message
      // }
    },
    handleModalOpen() {
      this.modalOpen = !this.modalOpen
    },
  },
}
