export default {
  name: 'PwdInputWrap',
  props: {
    field: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      eyeOpen: false,
      userInput: '',
      errorMessage: '',
    }
  },
  methods: {
    handleEyeOpen() {
      this.eyeOpen = !this.eyeOpen
    },
    async handleInput(event) {
      this.$emit('input', event.target.value)
      this.errorMessage = await this.$validateFunctions.inputValidate(
        this.field,
        this.userInput
      )
    },
  },
}
